.gallery-mobile {
  &__image {
    width: 90vw;
    height: 90vh;
    object-fit: contain;
    border-radius: 15px;
  }

  &__wrapper {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: black;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
  }
  &__description {
    width: 40%;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 10px 5px;
    background: #fff;
    z-index: 20;
    border-radius: 0 5px 0px 0;
  }
}

