.social-links__list{display:flex;align-items:center;justify-content:center;padding:10px;margin-top:20px}.social-links__item+.social-links__item{margin-left:20px}.social-links__link{display:inline-block;position:relative;padding:10px 5px;font-family:"fontAwesome"}.social-links__link::before{content:"";position:absolute;top:0;left:0;width:100%;height:0;border-bottom:2px solid #45f3ff;transition:.5s ease-in-out;text-align:center;line-height:61px;font-size:40px;color:#45f3ff;overflow:hidden}.social-links__link:hover::before{height:100%}.social-links__link .fa{font-size:40px;-webkit-text-stroke:1px #45f3ff;color:rgba(0,0,0,0)}.social-links__link-github::before{content:""}.social-links__link-linkedin::before{content:""}.social-links__link-telegram::before{content:""}.social-links__icon{width:30px;height:30px;transition:.2s;-webkit-text-stroke:2px red;fill:rgba(0,0,0,0)}.social-links__icon:hover{fill:#d400d4}