.progressbar {
  margin-top: 5px;
  height: 10px;
  border-radius: 5px;
  position: relative;
  -webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
  transition: 0.5s;

  &-1 {
    &::before {
      background: linear-gradient(90deg, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094);
    }
    &::after {
      background: linear-gradient(90deg, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094);
    }
  }
  &-2 {
    &::before {
      background: linear-gradient(90deg, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff);
    }
    &::after {
      background: linear-gradient(90deg, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff);
    }
  }
  &-3 {
    &::before {
      background: linear-gradient(90deg, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00);
    }
    &::after {
      background: linear-gradient(90deg, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00);
    }
  }
  &-4 {
    &::before {
      background: linear-gradient(90deg, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00);
    }
    &::after {
      background: linear-gradient(90deg, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00, #fb0094, #0000ff, #00ff00, #ffff00);
    }
  }
  &::before {
    content: '';
    position: absolute;
    inset: 0;
    animation: animateSkill 30s linear infinite;
    background-size: 500%;
    border-radius: 5px;

  }
  &::after {
    content: '';
    position: absolute;
    inset: 0;
    animation: animateSkill 30s linear infinite;
    background-size: 500%;
    filter: blur(20px);
  }
}
